@use "global" as *;


.category2 {
  display: inline-block;
  leading-trim: both;
  text-edge: cap;
  text-overflow: ellipsis;
  font-family: $font-family-ja-1;
  font-size: rem(14);
  font-weight: $bold;
  line-height: 1.4;

  padding-block: rem(4);
  padding-inline: rem(8);
  border: 1px solid $color-font-base-1;

  @include mq(md) {
    font-size: rem(12);
  }
}
