@use "global" as *;


.post {
  display: block;
}

.post:hover .post__eye-catch img {  // hover
  transform: scale(1.1);
}

.post__wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: rem(12);
}

.post__eye-catch {
  overflow: hidden;
}

.post__eye-catch img {
  aspect-ratio: 1 / 1;
  max-width: rem(222);
  width: 100%;
  object-fit: cover;

  transition: transform 0.2s ease-out;

  @include mq(md) {
    max-width: rem(164.5);
  }
}

.post__contents {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: rem(12);
  width: 100%;

  @include mq(md) {
    gap: rem(6);
  }
}

.post__top {
  position: relative;
  width: 100%;

  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: rem(20);  // 最低限のgapを指定

  @include mq(md) {
    gap: rem(8);  // 最低限のgapを指定
  }
}

.post__category {

}

.post__glass-icon {  // 虫眼鏡アイコン
  display: grid;
  place-items: center;
}

.post__glass-icon svg {
  max-width: rem(35);
  width: 100%;
  // position: absolute;
  // top: rem(-8);
  // right: 0;

  @include mq(md) {
    max-width: rem(24);
  }
}

.post__bottom {

}

.post__text {
  leading-trim: both;
  text-edge: cap;
  text-overflow: ellipsis;
  font-size: rem(14);
  font-weight: $bold;
  line-height: 1.4;

  @include mq(md) {
    font-size: rem(12);
  }
}
