@use "global" as *;


.posts {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: rem(64);
  row-gap: rem(49);
  align-items: flex-start;  // 上揃えに。

  @include mq(md) {
    grid-template-columns: repeat(2, 1fr);
    column-gap: rem(15.5);
    row-gap: rem(32);
  }
}

