@use "global" as *;


.more-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: rem(17);
  transition: opacity 0.2s ease-out;
}

.more-btn:hover {  // hover
  opacity: 0.6;
}

.more-btn__icon {
  display: grid;
}

.more-btn__icon svg {
  aspect-ratio: 1 / 1;
  max-width: rem(24);
  width: 100%;
}

.more-btn__text {
  text-align: center;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(20);
  font-weight: $bold;
  line-height: 1.4;
  letter-spacing: 0.04em;
  text-indent: 0.04em;
  position: relative;
}

.more-btn__text::after {  // 下線
  content: "";
  width: 100%;
  height: 1px;
  background: $color-font-base-1;
  position: absolute;
  bottom: rem(4);
  left: 50%;
  transform: translateX(-50%);
}




// =======================================
// モディファイア
// =======================================
// more-btn--only-sp
.more-btn--only-sp.more-btn {
  display: none;

  @include mq(md) {
    display: flex;
  }
}
