@use "global" as *;


.before-after {
  background: #FFF7EF;
  padding-top: rem(75);
  padding-bottom: rem(67);

  @include mq(md) {
    padding-top: rem(60);
    padding-bottom: rem(64);
  }
}

.before-after__inner {

}

.before-after__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: rem(20);
  border-bottom: 1px solid #B9A89B;

  @include mq(md) {
    display: block;
    padding-bottom: 0;
    border-bottom: none;
  }
}

.before-after__title {
  @include mq(md) {
    margin-inline: auto;
  }
}

.before-after__btn-pc {
  @include mq(md) {
    display: none;
  }
}

.before-after__category-sp {
  display: none;

  @include mq(md) {
    margin-top: rem(32);
    display: block;
    width: 100vw;
    margin: 0 calc(50% - 50vw);  // 親要素をはみ出るように指定。(『width: 100vw;』と併用で適用可能)
  }
}

.before-after__bottom {
  margin-top: rem(48);
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @include mq(md) {
    margin-top: rem(39);
    display: block;
  }
}

.before-after__category-pc {
  width: 20%;

  @include mq(md) {
    display: none;
  }
}

.before-after__posts {
  width: 73.5%;

  @include mq(md) {
    width: 100%;
  }
}

.before-after__btn-sp {
  display: none;

  @include mq(md) {
    margin-top: rem(40);
    display: flex;
  }
}
