@use "global" as *;


.access {
  background: #FFF7EF;
  padding-top: rem(80);
  padding-bottom: rem(96.64);

  @include mq(md) {
    padding-top: rem(64);
    padding-bottom: rem(100);
  }
}

.access__inner {
  @include mq(md) {
    max-width: rem(calc(550 + 24.5 * 2));  // 両端のpadding分を考慮
    margin-inline: auto;
  }
}

.access__wrap {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @include mq(md) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.access__left {
  margin-top: rem(22);

  @include mq(md) {
    text-align: center;
    margin-top: 0;
  }
}

.access__title {
  @include mq(md) {
    margin-inline: auto;
  }
}

.access__info {
  margin-top: rem(30);
  leading-trim: both;
  text-edge: cap;
  font-size: rem(16);

  @include mq(md) {
    text-align: center;
    font-size: rem(14);
  }
}

.access__access {
  margin-top: rem(22);
  leading-trim: both;
  text-edge: cap;
  font-size: rem(16);

  @include mq(md) {
    text-align: center;
    font-size: rem(14);
  }
}

.access__right {

}

.access__right iframe {
  aspect-ratio: 560 / 346;
  max-width: rem(560);
  width: rem(560);
  object-fit: cover;

  @include mq(md) {
    margin-top: rem(40);
    aspect-ratio: 345 / 213;
    max-width: rem(345);
    width: rem(345);
  }
}
