@use "global" as *;


.categories {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: rem(30);
}

.categories__item {
  color: #B9A89B;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-ja-1;
  font-size: rem(16);
  font-weight: $bold;
  line-height: 1.5;
  transition: opacity 0.2s ease-out;
}

.categories__item:hover {  // hover
  opacity: 0.7;
}

.categories__item.is-selected {  // 選択中のカテゴリ
  color: $color-font-base-1;
  padding-left: rem(28);
  position: relative;
}

.categories__item.is-selected::before {  // 選択中のカテゴリの左側の線
  content: "";
  width: rem(16);
  height: 1px;
  background: $color-font-base-1;
  position: absolute;
  left: 0;
  top: rem(12);
}
