@use "global" as *;


.cta2 {
  background: #FFF7EF;
}

.cta2__inner {

}

.cta2__wrap {

}

.cta2__item {
  display: block;
  padding-top: rem(90);
  padding-bottom: rem(65);

  border-top: 1px solid $color-font-base-1;
  transition: opacity 0.2s ease-out;

  @include mq(md) {
    padding-block: rem(45);
  }
}

.cta2__item:hover {  // hover
  opacity: 0.8;
}

.cta2__item:hover .cta2__item-arrow {  // hover
  transform: translateX(rem(10));
}

.cta2__item-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include mq(md) {
    display: block;
    max-width: rem(calc(550 + 24.5 * 2));  // 両端のpadding分を考慮
    margin-inline: auto;
  }
}

.cta2__item:last-of-type {
  border-bottom: 1px solid $color-font-base-1;
}

.cta2__item-left {
  color: #7B695A;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(39);
  font-weight: $bold;
  line-height: 1.4;
  letter-spacing: 0.04em;
  text-indent: 0.04em;
  text-transform: capitalize;

  @include mq(md) {
    display: none;
  }
}

.cta2__item-title {

}

.cta2__item-right {
  width: 51.66%;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include mq(md) {
    width: 100%;
  }
}

.cta2__item-texts {

}

.cta2__item-text1 {
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-ja-1;
  font-size: rem(32);
  font-weight: $bold;
  line-height: 1.5;

  @include mq(md) {
    font-size: rem(20);
  }
}

.cta2__item-text2 {
  margin-top: rem(3);
  color: #B9A89B;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(16);
  font-weight: $bold;
  line-height: 1.4;
  letter-spacing: 0.04em;
  text-indent: 0.04em;
  text-transform: capitalize;

  @include mq(md) {
    margin-top: rem(10);
    font-size: rem(16);
  }
}

.cta2__item-arrow {
  transition: transform 0.2s ease-out;
}

.cta2__item-arrow svg {
  width: rem(75);
}
