@use "global" as *;


.categories2 {

  @include mq(md) {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    gap: rem(24);

    overflow-x: scroll;
    white-space: nowrap;  /* テキストを折り返さない */
    scrollbar-width: thin;  /* スクロールバーを細く設定（モダンブラウザ対応） */
    padding-inline: rem(24);
    border-bottom: 2px solid #DFE0DF;

    position: relative;
    z-index: 5;
  }
}

.categories2::after {  // 下線

  // @include mq(md) {
  //   content: "";
  //   width: 100%;
  //   height: 2px;
  //   background: #DFE0DF;
  //   position: absolute;
  //   left: 50%;
  //   // bottom: rem(-2);
  //   bottom: 0;
  //   transform: translateX(-50%);
  //   z-index: 5;
  // }
}

.categories2__item {

  @include mq(md) {
    leading-trim: both;
    text-edge: cap;
    font-family: $font-family-ja-1;
    font-size: rem(14);
    font-weight: $bold;
    line-height: 1.5;
    padding-bottom: rem(24);

    // border-bottom: 2px solid #7B695A;
    position: relative;
    // z-index: 10;
  }
}

.categories2__item::after {  // 下線

  @include mq(md) {
    content: "";
    width: 100%;
    height: 2px;
    background: #7B695A;
    position: absolute;
    left: 50%;
    bottom: rem(-2);
    transform: translateX(-50%);
    z-index: 10;
  }
}
